.share{
    width: 100%;
    height: 170px;
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 16px -8px rgba(0,0,0,0.68); 
    box-shadow: 0px 1px 16px -8px rgba(0,0,0,0.68);
}

.shareWrapper{
    padding: 10px;
}

.shareTop {
    display: flex;
    align-items: center;
    margin-left: 20px;
    margin-top: 10px;
}

.shareProfileImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px
}

.shareInput{
    border: none;
    width: 80%;
}

.shareInput:focus{
    outline: none;
}

.shareHr{
    margin: 20px;
}

.shareBottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.shareOptions{
    display: flex;
    margin-left: 20px;
}

.shareOption{
    display: flex;
    align-items: center;
    margin-right: 15px;
    cursor: pointer;
}

.shareIcon{
    font-size:18px;
    margin-right: 3px;
}

.shareOptionText{
    font-size: 14px;
    font-weight: 500px;
}

.shareButton{
    border: none;
    padding: 7px;
    border-radius: 5px;
    background-color: #bb8f60;
    font-weight: 500;
    margin-right: 20px;
    cursor:pointer;
    color: white;
}
